.carousel{
    max-width: 100%;
    margin:auto;
    border-radius:10px;
    width:100%;
    height:100%;
    @include mediaquery(map-get($queries, lg)) {
        max-height:270px;
      }
    
      @include mediaquery(map-get($queries, xl)) {
        max-height:300px;
    }
}

.carousel-inner{
    border-radius:10px;
    height:100%;
}

.carousel-item{
    min-height:300px;
    max-height:300px;
}

.d-block{
    display:block;
    height:auto;
    max-height:100%;
    left:50%;
    position:absolute;
    top:50%;
    transform:translate(-50%,-50%);
    width:auto;
    min-width:100%;
}
